.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
    border-radius: 25px;
}

.project-card img{
    width: 100%;
}

.project-title {
    color: #fff;
    padding: 1rem 0;

}

.pro-details p {
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}

.pro-btns {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}

@media screen and (max-width: 740px){
    .project-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}